body {
  font-size: calc(14px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
}

.about-image {
  position: relative;
  display: table-cell;
  width: 50%;
  height: 100vmin;
  background-size: cover;
  background-position: 50% 20%;
  float: left;
}

.about-text {
  font-family: 'Roboto Slab', serif;
  vertical-align: middle;
  display: table-cell;
  width: 50%;
  align-items: center;
}

h1, h2, h3 {
  margin-top: 20px;
  margin-bottom: 10px;
  text-align: center;
}

.pull-left  {
  text-align: left;
}

header  {
  width: 100%;
  display: table;
  padding: 10px;
  margin: -10px;
  text-align: center;
}

header p {
  width: 100%;
  display: table-cell;
  text-align: left;
}

header span {
  display: block;
}

.footer {
  display: table;
  height: 100px;
  width: 100%;
  text-align: center;
}

.footer span {
  display: table-cell;
  vertical-align: middle;
}

.footer a { 
  text-decoration: none;
}

.footer a:hover { 
  text-decoration: none;
}

/* On hover, add a black background color with a little bit see-through  */
.about-image:hover, .about-image:hover {
  cursor: pointer;
  box-shadow: 0 0 11px rgba(33,33,33,.2); 
}

#id1  {
  background-image: url(./img/1.jpg);
}

#id2 {
  background-image: url(./img/2.jpg);
  background-position: 90% 50%;
}

#id3 {
  background-image: url(./img/3.gif);
  background-position: 73% 50%;
}

/* Caption text  */
.caption-text {
  color: #f2f2f2;
  font-size:  16px;
  padding: 8px 15px;
  position: absolute;
  bottom: 0;
  text-align: center;
  width: 100%;
  text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
}

@media screen and (max-width: 900px)  {
  .about-image {
    display: block;
    width: 100%;
    height: 450px;
    background-size: cover;
    background-position: 50% 40%;
    float:left;
  }

  .about-image id2 {
    background-position: 60% 40%;
  }

  .about-image id2 {
    background-position: 50% 50%;
  }

  .about-text {
    padding: 20px;
    align-items: center;
    vertical-align: middle;
  }

}

.hidden
{
  display: none;
}